import DonutChart from "react-donut-chart"
import './YumDonut.css'


interface YumDonutProps {
    label: string
    value: number
    color?: string
} 

const YumDonut = ({label, value, color='#f44336'}: YumDonutProps) => {
    // const colors = ['#f44336', '#e91e63', '#9c27b0', '#673ab7', '#3f51b5', '#2196f3', '#03a9f4', '#00bcd4', '#009688', '#4caf50', '#8bc34a', '#cddc39', '#ffeb3b', '#ffc107', '#ff9800', '#ff5722', '#795548', '#607d8b' ]

    return <DonutChart
    colors = {[color]}
    legend={false}
    width={120}
    height={120}
    emptyOffset={0.02}
    outerRadius={0.9}
    innerRadius={0.8}
    formatValues={(values, total) => ``}
    data={[
        {
            label,
            value,
        },
        {
            label: '',
            value: 100 - value,
            isEmpty: true,
        },
    ]}
    />
}

export default YumDonut