import * as React from 'react';
import Box from '@mui/material/Box';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Link from '@mui/material/Link';
import { Container, Paper } from '@mui/material';

interface LinkTabProps {
  label?: string;
  href?: string;
}

function LinkTab(props: LinkTabProps) {
  return (
    <Tab
      component="a"
      onClick={(event: React.MouseEvent<HTMLAnchorElement, MouseEvent>) => {
        // event.preventDefault();
      }}
      {...props}
    />
  );
}

export default function NavTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  return (
    <Paper elevation={6} style={{borderRadius: '30px', marginBottom: '20px'}}>
        <Tabs value={value} onChange={handleChange} aria-label="nav tabs" centered>
            <LinkTab label="" href="#intro" />
            <LinkTab label="" href="#education" />
            <LinkTab label="" href="#experience" />
            <LinkTab label="" href="/" />
        </Tabs>
    </Paper>
  );
}